import {EventEmitter, Injectable} from '@angular/core';
import {Ingredient} from "../shared/ingredient.model";
import {Subject} from "rxjs/Subject";

@Injectable()
export class ShoppinglistService {

  ingredientChangeEvent = new Subject<Ingredient[]>();
  editItemSubject = new Subject<number>();

  private ingredients: Ingredient[] = [
    new Ingredient('apples',5),
    new Ingredient('tomatoes',10),
  ];

  constructor() { }

  getIngredients(){

    console.log(this.ingredients.slice());
    return this.ingredients.slice();
  }

  getIngredientsbyId(id){
    return this.ingredients[id];
  }

  onAddClick(shoppingData){
    this.ingredients.push( new Ingredient(shoppingData.name, shoppingData.amount));
    // console.log(shoppingData.name);
     console.log(this.ingredients);
    this.ingredientChangeEvent.next(this.ingredients.slice());
  }

  onDeleteClick(){
    this.ingredients.pop();
  }

  onClearClick(){
    this.ingredients.length = 0 ;
  }

  addIngredientsFromRecipe(ingredients: Ingredient[]){

    this.ingredients.push(...ingredients);
    console.log(this.ingredients);
    this.ingredientChangeEvent.next(this.ingredients.slice());

  }

  updateIngredient(index: number, newingredients: Ingredient){
    this.ingredients[index] = newingredients;
    this.ingredientChangeEvent.next(this.ingredients.slice());

  }

  deleteingredient(index: number){
    this.ingredients.splice(index, 1);
    this.ingredientChangeEvent.next(this.ingredients.slice());
  }


}



