import {EventEmitter, Injectable} from '@angular/core';
import {Recipe} from "./recipe.model";
import {Ingredient} from "../shared/ingredient.model";
import {Subject} from "rxjs/Subject";

@Injectable()
export class RecipeService {

  // recipeSelectedEvent = new EventEmitter<Recipe>();
  recipesChanged = new Subject<Recipe[]>();

  private recipes: Recipe[] = [
    new Recipe('Salad ',
      ' Green and healthy ' ,
      'http://www.seriouseats.com/recipes/assets_c/2016/05/20160503-fava-carrot-ricotta-salad-recipe-1-thumb-1500xauto-431710.jpg',
      [
        new Ingredient('Spinach',20),
        new Ingredient('Bread',2)
      ]),
    new Recipe('Chicken Stew ',
      ' Tasty and Delicious ' ,
      'http://www.seriouseats.com/recipes/assets_c/2015/01/20150119-pressure-cooker-chicken-stew-food-lab-11-thumb-1500xauto-418088.jpg',
      [
        new Ingredient('Chicken',1),
        new Ingredient('Stew',1)
      ])
  ];

  constructor() { }

  getRecipe(){

    return this.recipes.slice();
  }

  getRecipeById(id){

    return this.recipes[id];
  }

  addRecipe(recipe: Recipe){
    this.recipes.push(recipe);
    this.recipesChanged.next(this.recipes.slice());
  }

  updateRecipe(index: number, newRecipe: Recipe){

    this.recipes[index] = newRecipe;

    this.recipesChanged.next(this.recipes.slice());


  }

  deleteRecipe(index: number){

    this.recipes.splice(index,1);
    this.recipesChanged.next(this.recipes.slice());
  }

  setRecipe(recipe: Recipe[]){

    this.recipes = recipe;
    this.recipesChanged.next(this.recipes.slice());
  }

}



