import {Component, OnDestroy, OnInit, ViewChild} from '@angular/core';
import {ShoppinglistService} from "../shoppinglist.service";
import {NgForm} from "@angular/forms";
import {Subscription} from "rxjs/Subscription";

@Component({
  selector: 'app-shopping-edit',
  templateUrl: './shopping-edit.component.html',
  styleUrls: ['./shopping-edit.component.css']
})
export class ShoppingEditComponent implements OnInit, OnDestroy {

  @ViewChild('f') shoppingForm: NgForm;

  subscription: Subscription;
  editMode = false;
  editItemIndex: number;
   editItem: any;



  constructor( private shoppingListService : ShoppinglistService) { }

  ngOnInit() {
    this.subscription = this.shoppingListService.editItemSubject.subscribe(
      (index: number) => {
        this.editItemIndex = index;
        this.editMode = true;
        this.editItem = this.shoppingListService.getIngredientsbyId(index);
        this.shoppingForm.setValue({
          name: this.editItem.name,
          amount: this.editItem.amount
        });
      }
    );
  }

  onAddClick(form: NgForm){



    if(this.editMode){
      this.shoppingListService.updateIngredient(this.editItemIndex, {name: form.value.name, amount: form.value.amount});

    } else {

      this.shoppingListService.onAddClick(
        {name: form.value.name, amount: form.value.amount});

    }

    this.editMode = false;

    form.resetForm();


  }

  onDeleteClick(){


    this.shoppingListService.deleteingredient(this.editItemIndex);
    this.onClear();
  }
  onClear(){

    this.shoppingForm.reset();
    this.editMode = false;
  }

  ngOnDestroy(){
    this.subscription.unsubscribe();
  }

}



